body {
  background-color: #282c34;
}

.App {
  text-align: center;
  margin: 0 auto;
}

.App header {
  background: #111;
  height: 9vh;
  color: white;
  display: flex;
  position: fixed;
  width: 95vw;
  top: 0;
  border-radius: 25px;
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: space-between;
  z-index: 99;
  padding-left: 1vw;
  padding-right: 1vw;
  padding-top: 1vh;
  padding-bottom: 1vh;
  margin: 0px 1.5vw;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(17, 17, 17, 0.75);
}

.App footer {
  background: #111;
  height: 10%;
  color: white;
  display: flex;
  position: fixed;
  width: 75%;
  bottom: 0;
  border-radius: 25px;
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: space-between;
  margin-left: -35%;
  padding: 10px;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background: #333;
}

main {
  padding: 10px;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: #1e1e24;
}

main::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 10%;
  width: 100%;
  display: flex;
  font-size: 1.5rem;
  align-self: center;
  align-items: center;
}

form button {
  width: 10%;
  background-color: #222;
}


input.msg {
  line-height: 1.5;
  width: 100%;
  font-size: 1.5rem;
  background: #222;
  color: white;
  outline: none;
  border: none;
  border-radius: 25px;
  padding: 2%;
  margin: 1%;
}

button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
  border-radius: 25px;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.sign-in {
  color: #282c34;
  background: white;
  max-width: 400px;
  font-size: 1.5rem;
  position: absolute;
  top: 65%;
  left: 25%;
  background: linear-gradient(45deg, orange, red);
}

.sign-out {
  background: rgb(255, 100, 100);
  font-size: 24px;
}


ul, li {
  text-align: left;
  list-style: none;
}

p.msg-date {
  color: grey;
  margin-bottom: 0px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  text-align: center;
}

p {
  margin-bottom: 0px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
  font-family: 'Roboto';
}

.message {
  display: flex;
  align-items: center;
  line-height: 24px;
  word-wrap: normal;
}


.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

.namesent {
  margin-bottom: 0px;
  line-height: 1;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: end;
  align-self: flex-end;
}

.namereceived {
  margin-bottom: 0px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: start;
  align-self: flex-start;
  line-height: 1;
}

img.pfp {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  margin: 2px 5px;
}

sign-in-text {
  color: white;
  text-align: center;
  font-size: 1.5rem;
  padding: 10px;
}

message-bar {
  background: #111;
  height: 10%;
  display: flex;
  position: fixed;
  width: 85%;
  bottom: 0;
  border-radius: 25px;
  justify-content: space-between;
  transform: translate(-50%, 0%);
  padding: 10px;
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(17, 17, 17, 0.75);
}

message-bar-form {
  
}

message-bar-displayname {
  color: white;
}

message-bar-upload-file {
  height: 10%;
  width: 10%;
}

message-bar-send-button {
  background: #0b93f6;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
  border-radius: 100%;
}

label.upload-file {
  background: #333;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
  border-radius: 100%;
}

content-area-2 {
  background: #222;
  height: 25vh;
  color: white;
  display: flex;
  position: fixed;
  width: auto;
  top: 50vh;
  border-radius: 25px;
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: space-between;
  padding: 25px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(34, 34, 34, 0.75);
}

content-area-1 {
  background: #111;
  height: auto;
  color: white;
  display: flex;
  position: fixed;
  width: auto;
  top: 0;
  border-radius: 25px;
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: space-between;
  padding: 25px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(17, 17, 17, 0.75);
}

blur-area {
  height: 100vh;
  width: 100vw;
  position: fixed;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  blur-area {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(34, 34, 34, 0.5);
  }
}

/* fallback */
@font-face {
  font-family: 'Material Icons Round';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialiconsround/v100/LDItaoyNOAY6Uewc665JcIzCKsKc_M9flwmP.woff2) format('woff2');
}

.material-icons-round {
  font-family: 'Material Icons Round';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
}

.error {
  color: #cc0000;
  font-size: 50px
}